#visual {
    /* border:5px solid black; */
    width: 100%;
    height: 100%;
}

#visual video {
    /* border:5px solid lightblue; */
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: 50% 0px;
}

#contents {
    /* border:5px solid red; */
    width: 100%;
    height: auto;
}

/* c01 */
#contents .c01 {
    /* border:5px solid green; */
    width: 100%;
    height: auto;
}

#contents .c01 .outer {
    /* border:5px solid rgb(9, 0, 128); */
    /* 1920 : 1720 */
    width: 89.58%;
    height: 100%;
    margin: 0 auto;
}

#contents .c01 .outer .inner {
    /* border:5px solid orange; */
    /* 1720 : 1520 */
    width: 88.37%;
    height: 100%;
    margin: 0 auto;
}

#contents .c01 .outer .inner h3.Title {
    /* border:5px solid blue; */
    text-align: center;
    padding-top: 100px;
    padding-bottom: 50px;
}

#contents .c01 .outer .inner .NewProducts {
    /* border:5px solid magenta; */
    width: 100%;
    height: auto;
}

#contents .c01 .outer .inner .NewProducts .swiper-wrapper {
    /* border:5px solid magenta; */
}

#contents .c01 .outer .inner .NewProducts .swiper-wrapper .swiper-slide {
    /* border:5px solid magenta; */
    /* width: calc(96% / 4); */
    width: 24%;
    /* margin-right: calc(4% / 3); */
}

#contents .c01 .outer .inner .NewProducts .swiper-wrapper .swiper-slide:last-child {
    margin-right: 0;
}

#contents .c01 .outer .inner .NewProducts .swiper-wrapper .swiper-slide figure {
    /* border:5px solid rgb(21, 9, 80); */
    /* 랭킹 넘버 & 하트 아이콘 배치를 위해 */
    position: relative;
    overflow: hidden;
}

#contents .c01 .outer .inner .NewProducts .swiper-wrapper .swiper-slide figure img {
    /* border:1px solid orange; */
    display: block;
    width: 100%;
    transition: 0.5s ease-in-out;
}

#contents .c01 .outer .inner .NewProducts .swiper-wrapper .swiper-slide figure img:hover {
    /* border:1px solid #7f0019; */
    scale: 1.2;
}

#contents .c01 .outer .inner .NewProducts .swiper-wrapper .swiper-slide figure figcaption {
    overflow: hidden;
    position: relative;
    color: #000;
    width: 101%;
    background: #fff;
}

/* progressbar */
.swiper-pagination-progressbar {
    margin: 100px 0;
    position: static;
    left: 0 !important;
    bottom: 0 !important;
    top: auto !important;
    width: 100% !important;
    height: 4px !important;
}


