#contents div.c04 div.outer div.inner p {
    /* border:5px solid rgb(0, 255, 68); */
    text-align: right;
    padding-bottom: 30px;
}

#contents div.c04 div.outer div.inner p a {
    /* background:lightblue; */
    display: inline-block;
    font-family: 'Pretendard-Regular';
    font-style: normal;
    font-weight: 700;
    font-size: 16px;
    letter-spacing: 20;
    color: #333;
    text-decoration-line: underline;
    text-decoration-thickness: 3px;
    text-underline-offset: 6px;
}

#contents div.c04 div.outer div.inner div.News {
    /* border:5px solid magenta; */
    width: 100%;
    height: auto;
    margin: 0 auto;
    /* overflow: hidden; */
}

#contents div.c04 div.outer div.inner div.News::after {
    content: "";
    display: block;
    /* clear : float 제거 */
    /* clear 속성 : right, left, both */
    clear: both;
}

#contents div.c04 div.outer div.inner div.News dl {
    /* border:5px solid rgb(134, 182, 221); */
    /* background: var(--maincolor); */
    background: #fff;
    width: calc(94% / 4);
    margin-right: calc(6% / 3);
    height: 400px;
    float: left;
    /* x축 / y축 / 번짐 / 색상 */
    /* box-shadow: 10px 10px 10px rgba(204, 204, 204, 1.0); */
    box-shadow: 0px 0px 5px rgba(204, 204, 204, 1.0);
    /* border:1px solid #ebebeb; */
}

#contents div.c04 div.outer div.inner div.News dl:last-child {
    margin-right: 0;
}

#contents div.c04 div.outer div.inner div.News dl dd.N-Img {
    /* border:5px solid rgb(240, 89, 18); */
    width: 100%;
    height: 45%;
    filter: invert(100%);
}

#contents div.c04 div.outer div.inner div.News dl:nth-child(1) dd.N-Img {
    background: url(../assets/images/mail_white_icon.png) no-repeat 50% 60%;
    background-size: 20%;
}

#contents div.c04 div.outer div.inner div.News dl:nth-child(2) dd.N-Img {
    background: url(../assets/images/sale_percent_white_icon.png) no-repeat 50% 60%;
    background-size: 24%;
}

#contents div.c04 div.outer div.inner div.News dl:nth-child(3) dd.N-Img {
    background: url(../assets/images/IA_white_icon.png) no-repeat 50% 60%;
    background-size: 18%;
}

#contents div.c04 div.outer div.inner div.News dl:nth-child(4) dd.N-Img {
    background: url(../assets/images/global_icon_white.png) no-repeat 50% 60%;
    background-size: 20%;
}

