/* 자주 사용하는 클래스 */

/* 컨텐츠 대주제 폰트 */
.Title {
    font-family: 'Pretendard-Regular';
    font-style: normal;
    font-weight: 900;
    font-size: 48px;
    letter-spacing: -10;
    color: #000;
    line-height:1;
}

/* 컨텐츠 탭메뉴 폰트 */
.TapMenu {
    font-family: 'Pretendard-Regular';
    font-style: normal;
    font-weight: 500;
    font-size: 20px;
    letter-spacing: 20;
    color: #999;
    line-height:1;
}

/* 컨텐츠 탭메뉴 선택 시 */
.TapClick {
    font-family: 'Pretendard-Regular';
    font-style: normal;
    font-weight: 700 !important;
    font-size: 20px;
    letter-spacing: 20;
    color: #333 !important;
    line-height:1;
    text-decoration-line: underline;
    text-decoration-thickness: 3px;
    text-underline-offset: 9px;
    position: relative;
}
.TapClick::after {
    content: "";
    display:block;
    width: 9px;
    height: 9px;
    background:var(--maincolor);
    border-radius: 50%;
    position:absolute;
    top: 3px;
    right: 12px;
}

/* 상품 랭킹 넘버 */
.RankingNum {
    font-family: 'Pretendard-Regular';
    font-style: normal;
    font-weight: 500;
    font-size: 20px;
    letter-spacing: -10;
    color: #fff;
    line-height: 40px;
    text-align: center;
    position: absolute;
    top: 0;
    left: 0;
    width: 40px;
    height: 40px;
    background:#000;
    /* background:var(--maincolor); */
}

/* 상품명 폰트 */
.ProductName {
    font-family: 'Pretendard-Regular';
    font-style: normal;
    font-weight: 500;
    font-size: 16px;
    letter-spacing: -10;
    color: #333;
    line-height: 1;
    padding-top: 16px;
}

/* 가격 폰트 */
.Price {
    font-family: 'Pretendard-Regular';
    font-style: normal;
    font-weight: 700;
    font-size: 22px;
    letter-spacing: -10;
    color: #000;
    line-height: 1;
    padding-top: 10px;
}

/* 정가 표시 */
.Del {
    display: inline-block;
    font-family: 'Pretendard-Regular';
    font-style: normal;
    font-weight: 500;
    font-size: 16px;
    letter-spacing: -10;
    color: #bebebe;
    line-height: 1;
    padding-top: 4px;
    margin-left: 12px;
}

/* 상품 컬러 */
.ColorBox {
    display: inline-block;
    border: 1px solid #ebebeb;
    width: 15px;
    height: 15px;
    margin-top: 15px;
}

/* 상품별 컬러코드 */
.White {
    background: #fff;
}
.LightGray {
    background: #bebebe;
}
.DarkGray {
    background: #333;
}
.Black {
    background: #000;
}
.Ivory {
    background: #f1ece6;
}
.Beige {
    background: #a1857a;
}
.Brown {
    background: #574543;
}
.Khaki {
    background: #464531;
}
.Navy {
    background: #243141;
}
.IndyBlue {
    background: #435263;
}
.Purple {
    background: #4c4e5a;
}
.IndyPink {
    background: #c07480;
}
.Stripe {
    background: repeating-linear-gradient(to bottom,
            #000,
            #fff 5px,
            #000 5px,
            #fff 5px);
}

/* 하트 아이콘 */
.HeartIcon {
    background: url(../assets/images/wishlist_icon.svg) no-repeat 0 0;
    background-size: cover;
    width: 16px;
    height: 15px;
    /* figure에 겹침 */
    position: absolute;
    /* figcaption 오른쪽 상단에 배치 */
    /* top: 468px; */
    /* 피규어 높이 452, 피그캡션 높이 94 = 총 높이 546 */
    /* 546 : 468 */
    top: 85.71%;
    right: 0;
    z-index: 10;
}
.HeartIconClick {
    background: url(../assets/images/wishlist_click_icon.svg) no-repeat 0 0;
    background-size: cover;
    width: 16px;
    height: 15px;
    position: absolute;
    top: 85.71%;
    right: 0;
    z-index: 10;
}

/* swiper 네비게이션 */
:root {
    --swiper-navigation-size: 1.6rem;
}
.swiper-button-prev {
    color:#000;
    /* background:rgba(255, 255, 255, 0.5); */
    padding: 25px 25px;
    border-radius: 50%;
    width:24px;
    height:44px;
    position:absolute;
    top:226px;
    left:20;
}
.swiper-button-next {
    color:#000;
    /* background:rgba(255, 255, 255, 0.2); */
    padding: 25px 25px;
    border-radius: 50%;
    width:24px;
    height:44px;
    position:absolute;
    top:226px;
    right:20;
}
.swiper-button-prev:hover,
.swiper-button-next:hover {
    color:#000;
    background:rgba(255, 255, 255, 1);
}

/* swiper 페이지네이션 */
.pagination {
    display: inline-block;
    background: #ebebeb;
    width: 100%;
    height: 4px;
    margin-top: 50px;
    position: relative;
}
.swiper-pagination-progressbar .swiper-pagination-progressbar-fill {
    background: #333 !important;
}

