@font-face {
    font-family: 'Pretendard-Regular';
    src: url('https://fastly.jsdelivr.net/gh/Project-Noonnu/noonfonts_2107@1.1/Pretendard-Regular.woff') format('woff');
    font-style: normal;
    font-weight: 400;
  }
  
  :root {
    --maincolor: #7f0019;
  }
  
  /* header */
  
  header{
    width: 100%;
    height: auto;
    background: rgba(255, 255, 255, 1);
    position: fixed;
    top: 0;
    left: 0;
    z-index: 100;
    transition: box-shadow 0.3s ease-in-out;
  }
  
  header div.headerouter{
    width: 89.58%;
    height: 100px;
    margin: 0 auto;
    position: relative;
    overflow: hidden;
  }
  
  /* 로고 */
  
  header div.headerouter h1{
    /* border:1px solid orange; */
    position: absolute;
    top: 0;
    left: 0;
  }
  
  header div.headerouter h1 a{
    /* border:1px solid green; */
    display: inline-block;
    /* 로고 이미지 자체가 가진 아래 기본여백 -8px */
    padding: 35px 0 27px;
  }
  
  /* 네비게이션 */
  
  header div.headerouter nav{
     /* border:1px solid gray; */
    width: 100%;
    height: 100%;
  }
  
  header div.headerouter nav ul{
    /* border:1px solid cyan; */
    height: 100%;
    overflow: hidden;
    width:fit-content;
    margin:0 auto;
  }
  
  header div.headerouter nav ul li{
    /* border:1px solid orchid; */
    /* display:inline-block; */
    height: 100%;
    float: left;
  }
  
  header div.headerouter nav ul li a{
    /* background:red; */
    display: block;
    width: 100%;
    text-align: center;
    font-family: 'Pretendard-Regular';
    font-style: normal;
    font-weight: 500;
    font-size: 20px;
    letter-spacing: -10;
    color: #000;
    line-height: 1;
    padding: 40px 24px;
  }
  
  /* 아이콘메뉴 */
  
  header div.headerouter div.HeaderIconMenu {
    /* border:5px solid red; */
    overflow: hidden;
    position: absolute;
    top: 35px;
    right: 0;
  }
  
  /* 검색 아이콘 */
  
  header div.headerouter div.HeaderIconMenu div.SearchIcon {
    /* border:5px solid orange; */
    float: left;
    margin-right: 28px;
  }
  
  header div.headerouter div.HeaderIconMenu div.SearchIcon a {
    /* border:1px solid black; */
    display: inline-block;
    width: 30px;
    height: 30px;
    background: url(../assets/images/search_icon.svg) no-repeat 0 0;
    background-size: 100%;
  }
  
  /* 로그인, 위시리스트, 장바구니 아이콘 */
  
  header div.headerouter div.HeaderIconMenu ul.Icons {
    /* border:5px solid blue; */
    float: left;
  }
  
  header div.headerouter div.HeaderIconMenu ul.Icons li {
    /* border:5px solid rgb(218, 240, 92); */
    display: inline-block;
    margin-right: 24px;
  }
  
  header div.headerouter div.HeaderIconMenu ul.Icons li:last-child {
    /* border:5px solid rgb(218, 240, 92); */
    margin-right: 0px;
  }
  
  header div.headerouter div.HeaderIconMenu ul.Icons li a {
    /* border:1px solid black; */
    display: inline-block;
    width: 30px;
    height: 30px;
  }
  
  header div.headerouter div.HeaderIconMenu ul.Icons li:nth-child(1) a {
    background: url(../assets/images/login_icon.svg) no-repeat 0 0;
    background-size: 100%;
  }
  
  header div.headerouter div.HeaderIconMenu ul.Icons li:nth-child(2) a {
    background: url(../assets/images/wishlist_icon.svg) no-repeat 0 0;
    background-size: 100%;
  }
  
  header div.headerouter div.HeaderIconMenu ul.Icons li:nth-child(3) a {
    background: url(../assets/images/ecommerce_bag_icon.svg) no-repeat 0 0;
    background-size: 100%;
  }
  
  /* footer */
  
  footer {
    /* border:5px solid black; */
    width: 100%;
    height: auto;
    background-color: var(--maincolor);
  }
  
  footer div.footerouter {
    /* border:5px solid gray; */
    /* 1920 : 1720 */
    width: 89.58%;
    height: 100%;
    margin: 0 auto;
    overflow: hidden;
    /* 구분선 */
    background: url(../assets/images/footer_line.png) repeat-x 0 117px;
  }
  
  /* 오른쪽 영역 */
  
  footer div.footerouter div.RightArea {
    /* border:5px solid red; */
    width: 50%;
    height: 100%;
    float: left;
  }
  
  /* 고객센터, 매장정보, 개인정보 처리방침 */
  
  footer div.footerouter div.RightArea ul.FooterNavBar {
    /* border:5px solid rgb(92, 208, 243); */
    padding-top: 34px;
    padding-bottom: 45px;
  }
  
  footer div.footerouter div.RightArea ul.FooterNavBar li {
    /* border:5px solid rgb(58, 123, 49); */
    display: inline-block;
  }
  
  footer div.footerouter div.RightArea ul.FooterNavBar li a {
    /* background:lightblue; */
    display: inline-block;
    margin-right: -4px;
    font-family: 'Pretendard-Regular';
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    letter-spacing: -10;
    /* color: #000; */
    color:#fff;
    line-height: 1;
    padding: 16px 14px 17px;
  }
  
  footer div.footerouter div.RightArea ul.FooterNavBar li:first-child a {
    padding-left: 0;
  }
  
  /* 주소 및 저작권 */
  
  footer div.footerouter div.RightArea ul.CopyRight {
    /* border:5px solid rgb(105, 129, 233); */
    padding-top: 38px;
  }
  
  footer div.footerouter div.RightArea ul.CopyRight li {
    /* border:5px solid rgb(234, 255, 0); */
    font-family: 'Pretendard-Regular';
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    letter-spacing: -10;
    /* color: #666; */
    color:#fff;
    line-height: 1;
    padding-bottom: 10px;
  }
  
  footer div.footerouter div.RightArea ul.CopyRight li:last-child {
    /* border:5px solid rgb(255, 0, 187); */
    color: #ccc;
    padding-top: 88px;
    padding-bottom: 50px;
  }
  
  /* 왼쪽 영역 */
  
  footer div.footerouter div.LeftArea {
    /* border:5px solid rgb(8, 114, 8); */
    width: 50%;
    height: 100%;
    float: right;
  }
  
  /* 아이콘 메뉴 */
  
  footer div.footerouter div.LeftArea ul.FooterIconMenu {
    /* border:5px solid rgb(59, 47, 193); */
    text-align: right;
    padding-top: 33px;
    padding-bottom: 34px;
  }
  
  footer div.footerouter div.LeftArea ul.FooterIconMenu li {
    /* border:5px solid rgb(239, 134, 6); */
    display: inline-block;
    margin-right: 11px;
  }
  
  footer div.footerouter div.LeftArea ul.FooterIconMenu li:last-child {
    margin-right: 0px;
  }
  
  /* 인스타, 블로그, 패스포트, 매거진 아이콘 */
  
  footer div.footerouter div.LeftArea ul.FooterIconMenu li a {
    /* background:lightblue; */
    display: inline-block;
    width: 50px;
    height: 50px;
  }
  
  footer div.footerouter div.LeftArea ul.FooterIconMenu li:nth-child(1) a {
    background: url(../assets/images/instagram_white_icon.svg) no-repeat 0 0;
    background-size: cover;
  }
  
  footer div.footerouter div.LeftArea ul.FooterIconMenu li:nth-child(2) a {
    background: url(../assets/images/blog_white_icon.svg) no-repeat 0 0;
    background-size: cover;
  }
  
  footer div.footerouter div.LeftArea ul.FooterIconMenu li:nth-child(3) a {
    background: url(../assets/images/muji_passport_icon.png) no-repeat 0 0;
    background-size: cover;
  }
  
  footer div.footerouter div.LeftArea ul.FooterIconMenu li:nth-child(4) a {
    /* 디자인 요소입니다 (삭제X) */
    border: 2px solid #363636;
    background: url(../assets/images/muji_magazine_icon.png) no-repeat 0 0;
    background-size: cover;
  }
  
  /* 글로벌 사이트 */
  
  footer div.footerouter div.LeftArea form {
    /* border:5px solid rgb(237, 24, 180); */
    text-align: right;
    padding-top: 35px;
  }
  
  footer div.footerouter div.LeftArea form select {
    /* 디자인 요소입니다 (삭제X) */
    border: 1px solid #ebebeb;
    width: 140px;
    height: 30px;
    font-family: 'Pretendard-Regular';
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    letter-spacing: -10;
    color: #666;
    line-height: 1;
    padding-left: 4px;
  }
  
  /* 로고 */
  
  footer div.footerouter div.LeftArea h2 {
    /* border:5px solid rgb(40, 248, 203); */
    text-align: right;
    padding-top: 132px;
  }
  
  footer div.footerouter div.LeftArea h2 a {
    /* background:lightblue; */
    display: inline-block;
  }
  
  footer div.footerouter div.LeftArea h2 a img{
    /* background:lightblue; */
    display: block;
    filter: grayscale(100%) brightness(100%);
  }
  